* {
  box-sizing: border-box;
}

.container {
  text-align: center;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.timer-body {
  background-color: #e4f5f1;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  justify-content: center;
  display: flex;
}

.field {
  width: 100px;
  height: 100px;
  color: #0f0;
  background-color: #000;
  border-radius: 20px;
  flex-direction: column;
  margin-top: 15px;
  margin-right: 20px;
  display: flex;
}

.field:last-child {
  color: red;
  margin-right: 0;
}

.field:first-child {
  color: #ff0;
}

.value {
  font-size: 50px;
}

.input {
  height: 50px;
  text-align: center;
  color: #12a4ba;
  background-color: #e4f5f1;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 15px;
  outline-color: rgba(0, 0, 0, 0);
  margin-right: 15px;
  font-size: 28px;
  font-weight: 900;
}

.button {
  height: 50px;
  cursor: pointer;
  color: #fff;
  background-color: red;
  border: 2px solid #000;
  border-radius: 90px;
  outline-color: rgba(0, 0, 0, 0);
  font-size: 14px;
}

.button:hover {
  background-color: #c00;
  border-color: rgba(0, 0, 0, .3);
}

.input-box {
  text-align: center;
  justify-content: center;
  margin: 0 auto;
  display: flex;
}

.button:disabled {
  color: #d8d4d4;
  background-color: rgba(204, 0, 0, .514);
  border-color: #4b4a4a;
}

